.footer {
  padding: 109px 0 130px;
  background-image: linear-gradient(180deg, #010203 9.9%, rgba(1, 2, 3, 0) 100%), url("../img/footer-bg.jpg");
  background-repeat: no-repeat;
  background-position: top 0 left 0;
  background-size: cover; }
  @media (max-width: 1023px) {
    .footer {
      padding: 100px 0;
      min-height: auto; } }
  @media (max-width: 767px) {
    .footer {
      padding: 50px 0; } }
  @media (max-width: 479px) {
    .footer {
      padding: 30px 0; } }

.footer__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: ". content"; }
  @media (max-width: 1250px) {
    .footer__wrapper {
      grid-template-columns: 1fr;
      grid-template-areas: "content"; } }

.footer__content {
  display: flex;
  justify-content: space-between;
  grid-area: content; }
  @media (max-width: 1023px) {
    .footer__content {
      width: 100%; } }
  @media (max-width: 479px) {
    .footer__content {
      justify-content: flex-start;
      gap: 7px; } }

.footer__block {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 27%;
  min-height: auto; }
  .footer__block--left {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
    @media (max-width: 479px) {
      .footer__block--left {
        order: 1; } }
  .footer__block li {
    margin-bottom: 20px; }
    .footer__block li a {
      text-transform: uppercase; }
      .footer__block li a:not(:hover) {
        transition: 0.3s ease; }
      .footer__block li a:hover, .footer__block li a:focus {
        outline: none;
        cursor: pointer;
        transition: 0.3s ease;
        color: #cb0e0e; }
      @media (max-width: 767px) {
        .footer__block li a {
          font-size: 14px;
          line-height: 16px; } }
      @media (max-width: 599px) {
        .footer__block li a {
          font-size: 10px;
          line-height: 12px; } }
    @media (max-width: 599px) {
      .footer__block li {
        margin-bottom: 10px; } }
    @media (max-width: 479px) {
      .footer__block li {
        margin-bottom: 5px; } }
  .footer__block li:last-child {
    margin-bottom: 0; }
  @media (max-width: 1023px) {
    .footer__block {
      text-align: center; } }
  @media (max-width: 767px) {
    .footer__block {
      text-align: initial; } }
  @media (max-width: 479px) {
    .footer__block {
      width: 36%; } }

.footer__block-text {
  font-size: 17px;
  line-height: 19px;
  margin: 0; }
  @media (max-width: 767px) {
    .footer__block-text {
      font-size: 14px;
      line-height: 16px; } }
  @media (max-width: 599px) {
    .footer__block-text {
      font-size: 10px;
      line-height: 12px; } }
  @media (max-width: 479px) {
    .footer__block-text {
      font-size: 8px;
      line-height: 10px; } }

